import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoginComponent } from './components/login/login.component';
import { HomeComponent } from './components/home/home.component';
import { RedireccionesComponent } from './components/redirecciones/redirecciones.component';
import { VariablesComponent } from './components/variables/variables.component';
import { FormulariosComponent } from './components/formularios/formularios.component';
import { ComentariosComponent } from './components/comentarios/comentarios.component';
import { ComentariosPaginaComponent } from './components/comentarios-pagina/comentarios-pagina.component';
import { TracksComponent } from './components/tracks/tracks.component';


const routes: Routes = [
  { path: 'login', component: LoginComponent },
  { path: 'redirecciones', component: RedireccionesComponent },
  { path: 'variables', component: VariablesComponent },
  { path: 'formularios', component: FormulariosComponent },
  { path: 'comentarios', component: ComentariosComponent },
  { path: 'comentarios/:path', component: ComentariosPaginaComponent },
  { path: 'tracks', component: TracksComponent },
  { path: 'home', component: HomeComponent },
  { path: '**', component: HomeComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
