import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';

@Injectable()
export class HttpService {

  baseUrl:string;


  constructor(private http: HttpClient) {
    this.baseUrl = document.getElementsByTagName('base')[0].href;
  }

  login(username:string, password:string) {  
    return this.http.post(this.baseUrl + 'api/v1/auth/login', {
      username: username,
      password: password    
    });     
  }

  get(file:string) {  
    return this.http.get(this.baseUrl + 'api/v1/'+file+'/get');     
  }

  set(file:string, json:object) {  
    return this.http.post(this.baseUrl + 'api/v1/'+file+'/set', json);     
  }

  templates(){
    return this.http.get(this.baseUrl + 'api/v1/rutas/templates');     
  }

  getPaginasComentadas(){
    return this.http.get(this.baseUrl + 'api/v1/comments/pages');
  }

  getComentariosPagina(path:string) {  
    return this.http.post(this.baseUrl + 'api/v1/comments/get', {
      path: path    
    });     
  }

  setComentariosPagina(path:string, json:object) {  
    return this.http.post(this.baseUrl + 'api/v1/comments/set', {
      path: path,
      comments:json    
    });     
  }
  
 

  getTrack(month:number, year:number){
    return this.http.post(this.baseUrl + 'api/v1/tracks/get', {
      month: month, 
      year: year
    });
  }

}