import { Component, OnInit, ViewChild  } from '@angular/core';
import { ChartDataSets, ChartOptions } from 'chart.js';
import { Color, Label, BaseChartDirective } from 'ng2-charts';

import { HttpService } from '../../services/http.service';

@Component({
  selector: 'app-tracks',
  templateUrl: './tracks.component.html',
  styleUrls: ['./tracks.component.css']
})
export class TracksComponent implements OnInit {

  public lineChartData: ChartDataSets[] = [{ data: [0], label: 'Inicial' }];
  public lineChartLabels: Label[] = ['Inicial'];
  public lineChartOptions: (ChartOptions & { annotation: any }) = {
    responsive: true,   
    annotation: {},
    legend: {
      position : 'right'
    }
  };
  public lineChartColors: Color[] = [];
  public lineChartLegend = true;
  public lineChartType = 'line';
  public lineChartPlugins = [];

  @ViewChild(BaseChartDirective, { static: true }) chart: BaseChartDirective;

  annos:number[] = [2018, 2019, 2020];
  anno:number = new Date().getFullYear();

  meses:string[] = ['Anual', 'Enero', 'Febrero', 'Marzo', 'Abril', 'Mayo', 'Junio', 'Julio', 'Agosto', 'Septiembre', 'Octubre', 'Noviembre', 'Diciembre'];
  mes:number = 0;

  constructor( private httpService:HttpService ) { }

  ngOnInit() {
    this.loadData();
  }

  loadData() {

    this.httpService.getTrack(this.mes, this.anno).subscribe(
      res => {
        this.lineChartData = [];
        if(this.mes == 0){
          this.lineChartLabels = this.meses.slice(1);                    
        }else{
          this.lineChartLabels = [];
          for (let index = 1; index <= new Date(this.anno, this.mes, 0).getDate(); index++){
            this.lineChartLabels.push(index.toString());           
          }        
        }

        Object.keys(res).forEach(track => {
          let data = { data: [], label: track }
          this.lineChartLabels.forEach(mes => {
            data.data.push(res[track][mes]);
          });
          this.lineChartData.push(data);            
        });

        
                        
      },
      error => {
        console.error(error);        
      });
    this.chart.update();
  }


}
