import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { HttpService } from '../../services/http.service';
import { Comentario } from '../../models/comentario.model';


@Component({
  selector: 'app-comentarios-pagina',
  templateUrl: './comentarios-pagina.component.html',
  styleUrls: ['./comentarios-pagina.component.css']
})
export class ComentariosPaginaComponent implements OnInit {

  @ViewChild('closeModal', { static: false }) closeModal: ElementRef;

  path:string;
  comentarios:any;
  id:number;
  comentario:Comentario = {
    formName: "",
    formId: "",
    'csrf-token': "",
    nombre: "",
    email: "",
    comentario: "",
    date: "",
    validate: false
  };

  constructor(  private route: ActivatedRoute, 
                private httpService:HttpService
              ) { }

  ngOnInit() {

    this.path = 'comentarios/' + this.route.snapshot.paramMap.get('path');

    this.httpService.getComentariosPagina(this.path).subscribe(
      res => {
        this.comentarios = res;                
      },
      error => {
        console.error(error);        
      });
  }


  selComentario(comentario:Comentario, id:number){
    this.comentario = {...comentario};
    this.id = id;
  }

  delete(){
    if(confirm('¿Estas seguro de borrar el comentario?')){
      this.comentarios.splice(this.id, 1);
      this.saveComentarios();
    }
  }

  save(){
    this.comentarios[this.id] = this.comentario;
    this.saveComentarios()

  }

  saveComentarios(){
    this.httpService.setComentariosPagina(this.path, this.comentarios).subscribe(
      res => {
        this.id = null;
      },
      error => {
        console.error(error);        
      });
  }



}
